@font-face {
  font-family: "Roboto Regular";
  src: url(./fonts/Roboto-Regular.ttf);
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Medium";
  src: url(./fonts/Roboto-Medium.ttf);
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Bold";
  src: url(./fonts/Roboto-Bold.ttf);
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Karla";
  src: url(./fonts/Karla-VariableFont_wght.ttf);
  font-weight: 700;
  font-style: normal;
}
.App {
  max-width: 1068px;
  margin: 0 auto;
  padding: 0 24px;
}

.header {
  margin: 86px auto 15px;
  display: flex;
  justify-content: center;
  max-width: 600px;
}
.header img {
  width: 100%;
}

.search-form_input {
  width: 100%;
  height: 60px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  background: url(./components/SearchForm/Vector.png) no-repeat scroll 20px 20px;
  padding-left: 50px;
}

.characters {
  margin: 58px auto 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  gap: 20px;
}
@media (max-width: 992px) {
  .characters {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 576px) {
  .characters {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 360px) {
  .characters {
    grid-template-columns: 1fr;
  }
}

.character-link {
  display: block;
  height: 100%;
  border-radius: 4px;
  box-shadow: inset 0px 2px 4px 0px rgba(0, 0, 0, 0.1411764706), 0px 3px 4px 0px rgba(0, 0, 0, 0.1215686275), 0px 1px 5px 0px rgba(0, 0, 0, 0.2);
}

.character {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.character__image {
  width: 100%;
  flex: 0 0 auto;
  border-radius: 4px 4px 0 0;
}
.character__name {
  flex: 1 0 auto;
  margin: 10px 0 0px 16px;
  font-size: 20px;
  font-family: "Roboto Medium", sans-serif;
  line-height: 30px;
  color: rgba(0, 0, 0, 0.8705882353);
  letter-spacing: 0.15px;
}
.character__species {
  flex: 0 1 auto;
  margin: 0 0 10px 16px;
  font-size: 14px;
  font-family: "Roboto Regular", sans-serif;
  line-height: 21px;
  color: rgba(0, 0, 0, 0.6);
  letter-spacing: 0.25px;
}

.details__link {
  display: flex;
  align-items: center;
  position: absolute;
  top: 23px;
  left: 54px;
}
@media (max-width: 360px) {
  .details__link {
    top: 27px;
    left: 28px;
  }
}
.details__link_text {
  margin-left: 12px;
  font-size: 18px;
  font-family: "Karla", sans-serif;
  line-height: 21px;
  color: rgba(0, 0, 0, 0.8705882353);
  text-transform: uppercase;
}
.details__card {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.details__img {
  max-width: 300px;
  border-radius: 150px;
  margin: 76px 0 16px;
  border: 5px solid #F2F2F7;
}
@media (max-width: 576px) {
  .details__img {
    max-width: 200px;
    border-radius: 100px;
    margin: 100px 0 25px;
  }
}
@media (max-width: 360px) {
  .details__img {
    max-width: 160px;
    border-radius: 75px;
    margin: 124px 0 34px;
  }
}
.details__name {
  margin: 0 auto 48px;
  font-size: 48px;
  font-family: "Roboto Regular", sans-serif;
  line-height: 56.25px;
  color: rgba(0, 0, 0, 0.8705882353);
  text-align: center;
}
@media (max-width: 576px) {
  .details__name {
    font-size: 40px;
    line-height: 45px;
    margin: 0 auto 40px;
  }
}
@media (max-width: 360px) {
  .details__name {
    font-size: 32px;
    line-height: 38px;
    margin: 0 auto 34.47px;
  }
}
.details__info {
  margin-bottom: 48px;
  font-size: 20px;
  font-family: "Roboto Medium", sans-serif;
  line-height: 24px;
  color: #8E8E93;
  letter-spacing: 0.15px;
}
@media (max-width: 576px) {
  .details__info {
    margin-bottom: 35px;
    align-self: start;
  }
}
@media (max-width: 360px) {
  .details__info {
    margin-bottom: 25px;
    align-self: start;
  }
}
.details__list {
  min-width: 413px;
  padding-left: 16px;
}
@media (max-width: 576px) {
  .details__list {
    min-width: 95%;
    align-self: start;
  }
}
@media (max-width: 360px) {
  .details__list {
    align-self: start;
  }
}
.details__param {
  font-size: 16px;
  font-family: "Roboto Bold", sans-serif;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.8705882353);
  letter-spacing: 0.15px;
  border-bottom: 1px solid rgba(33, 33, 33, 0.08);
  padding-bottom: 12px;
}
.details__param:last-child {
  margin-bottom: 142px;
}
@media (max-width: 360px) {
  .details__param:last-child {
    margin-bottom: 85px;
  }
}
.details__param_value {
  font-size: 14px;
  font-family: "Roboto Regular", sans-serif;
  line-height: 20px;
  color: #6E798C;
  letter-spacing: 0.25px;
}/*# sourceMappingURL=App.css.map */